import { cast } from 'mobx-state-tree';

import { BaseArtist } from '@yandex-music-int/resource-collection';

import { DecomposedArtistModel } from '../model/DecomposedArtist.js';

export const decomposedArtistsResponseToModel = (
    decomposedArtists: Array<string | BaseArtist> = []
): DecomposedArtistModel[] | undefined => {
    const decomposed: DecomposedArtistModel[] = [];

    decomposedArtists.forEach((item: BaseArtist | string, index: number) => {
        const nextItem = decomposedArtists[index + 1];
        if (typeof item === 'string' && typeof nextItem === 'object') {
            decomposed.push(
                cast({
                    id: String(nextItem.id),
                    name: nextItem.name,
                    various: nextItem.various,
                    composer: nextItem.composer,
                    separator: item
                })
            );
        }
    });

    return decomposed.length > 0 ? decomposed : undefined;
};
