'use client';

import { clsx } from 'clsx';
import { ReactElement, MouseEvent } from 'react';
import { FormattedMessage } from 'react-intl';

import { Link } from '@yandex-music-int/sol/components/Link';

import styles from './SeparatedArtists.module.scss';

interface SpoilerProps {
    spoilerArtistsCount: number;
    handleOnSpoilerClick: (event: MouseEvent<HTMLAnchorElement>) => void;
    spoilerClassName?: string;
}

export const SeparatedArtistsSpoiler = ({
    spoilerArtistsCount: counter,
    spoilerClassName,
    handleOnSpoilerClick
}: SpoilerProps): ReactElement => {
    return (
        <>
            {' '}
            <Link
                role="button"
                href=""
                className={clsx(styles.spoiler, spoilerClassName)}
                onClick={handleOnSpoilerClick}
                rel="nofollow"
            >
                <FormattedMessage id="entity-names.number-of-more-artists" values={{ counter }} />
            </Link>
        </>
    );
};
