import { types, Instance, getRoot } from 'mobx-state-tree';

export const BaseArtist = types
    .model('BaseArtist', {
        id: types.string,
        name: types.string,
        various: types.maybe(types.boolean),
        composer: types.maybe(types.boolean)
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => ({
        toggleLike() {
            // @ts-ignore
            const { library, user } = getRoot(self);

            if (user.isAuthorized) {
                return library.toggleArtistLike({
                    entityId: self.id,
                    userId: user.account.uid
                });
            }
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get key() {
            return `${self.id}_${self.name}`;
        },

        get isLiked() {
            // @ts-ignore
            const { library } = getRoot(self);
            return library.isArtistLiked(self.id);
        }
    }));

export type BaseArtistModel = Instance<typeof BaseArtist>;
