import { ReactElement } from 'react';

import { BaseNotificationLike } from '@/shared/ui/index.js';

import { ArtistModel } from '../model/Artist.js';

interface Props {
    artist: ArtistModel;
    closeToast?: () => void;
}

export const NotificationLike = ({ artist, closeToast }: Props): ReactElement => {
    return (
        <BaseNotificationLike
            onClose={closeToast}
            entityVariant="artist"
            entityUrl={`/artist/${artist.id}`}
            collectionUrl="/collection/artists"
            coverUri={artist.coverUri}
            entityTitle={artist.name}
            isLiked={artist.isLiked}
        />
    );
};
