import { cast } from 'mobx-state-tree';

import { Track as TrackResponse } from '@yandex-music-int/resource-collection';

import { getSafeAverageColor, hexToHsl } from '@/shared/lib/index.js';

import { TrackModel } from '../model/Track.js';

import { trackParametersResponseToModel } from './trackParametersResponseToModel.js';

const DEFAULT_LIGHTNESS = 20;

export const trackResponseToModel = (response: TrackResponse | null | undefined): TrackModel => {
    response = (response || {}) as TrackResponse;

    const averageColor = getSafeAverageColor(response?.derivedColors);
    let hslAverageColor = '';
    if (averageColor) {
        const { h, s } = hexToHsl(averageColor);
        hslAverageColor = `hsl(${h}, ${s}%, ${DEFAULT_LIGHTNESS}%)`;
    }

    return cast({
        id: (response.id || 0).toString(),
        isAvailable: Boolean(response?.available),
        isRemoved: response?.error === 'not-found',
        title: response?.title ?? '',
        contentWarning: response?.contentWarning,
        version: response?.version,
        durationMs: response?.durationMs,
        coverUri: response?.coverUri,
        averageColor,
        hslAverageColor,
        trackParametrs: trackParametersResponseToModel(response?.trackParameters),
        trackSource: response?.trackSource
    });
};
