import { types, Instance } from 'mobx-state-tree';

import { Track } from './Track.js';

export const UgcTrack = Track.props({
    canPublish: types.maybe(types.boolean),
    desiredVisibility: types.maybe(types.string),
    ugcArtistName: types.maybe(types.string)
}).named('UgcTrack');

export type UgcTrackModel = Instance<typeof UgcTrack>;
