'use client';

import { clsx } from 'clsx';
import { ReactElement, ReactNode, useCallback, useMemo, useState, MouseEvent } from 'react';

import { CaptionSize } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';

import { ArtistModel } from '../../model/Artist.js';

import styles from './SeparatedArtists.module.scss';
import { SeparatedArtistsItem } from './SeparatedArtistsItem.js';
import { SeparatedArtistsSpoiler } from './SeparatedArtistsSpoiler.js';

interface Props extends WithClassName {
    artists?: ArtistModel[];
    separator?: string;
    lineClamp?: number;
    visibleArtistsCount?: number;
    spoilerClassName?: string;
    withLink?: boolean;
    linkClassName?: string;
    captionClassName?: string;
    captionSize?: CaptionSize;
    variant?: 'breakAll' | 'breakWord';
}

export const SeparatedArtists = ({
    className,
    artists = [],
    separator = ', ',
    lineClamp,
    visibleArtistsCount,
    withLink = true,
    spoilerClassName,
    linkClassName,
    captionClassName,
    captionSize,
    variant = 'breakAll'
}: Props): ReactElement => {
    const [isArtistsSpoilerOpened, setIsArtistsSpoilerOpened] = useState(false);

    const handleOnSpoilerClick = useCallback((event: MouseEvent<HTMLAnchorElement>) => {
        setIsArtistsSpoilerOpened(true);
        event.preventDefault();
    }, []);

    //      
    const artistsComponents = useMemo(() => {
        let visibleArtists = artists;
        if (visibleArtistsCount && !isArtistsSpoilerOpened) {
            visibleArtists = artists.slice(0, visibleArtistsCount);
        }

        return visibleArtists.reduce((accumulator: ReactNode[], artist: ArtistModel) => {
            const artistComponent = (
                <SeparatedArtistsItem
                    key={artist.key}
                    artist={artist}
                    linkClassName={linkClassName}
                    captionClassName={captionClassName}
                    withLink={withLink}
                    captionSize={captionSize}
                />
            );

            if (accumulator.length) {
                return accumulator.concat([separator, artistComponent]);
            }

            return [artistComponent];
        }, []);
    }, [
        artists,
        visibleArtistsCount,
        isArtistsSpoilerOpened,
        linkClassName,
        captionClassName,
        withLink,
        separator,
        captionSize
    ]);

    //  ,     
    const spoiler = useMemo(() => {
        if (!isArtistsSpoilerOpened && visibleArtistsCount && visibleArtistsCount < artists.length) {
            const spoilerArtistsCount = artists.length - visibleArtistsCount;

            return (
                <SeparatedArtistsSpoiler
                    spoilerClassName={spoilerClassName}
                    spoilerArtistsCount={spoilerArtistsCount}
                    handleOnSpoilerClick={handleOnSpoilerClick}
                />
            );
        }

        return;
    }, [artists.length, handleOnSpoilerClick, isArtistsSpoilerOpened, spoilerClassName, visibleArtistsCount]);

    return (
        <div
            style={{ WebkitLineClamp: lineClamp }}
            className={clsx(
                styles.root,
                styles[`root_variant_${variant}`],
                {
                    [styles.root_clamp]: lineClamp && lineClamp > 0
                },
                className
            )}
        >
            {artistsComponents}
            {spoiler}
        </div>
    );
};
