import { ReactElement } from 'react';

import { BaseNotificationLike } from '@/shared/ui/index.js';

import { TrackModel } from '../model/Track.js';

interface Props {
    track: TrackModel;
    closeToast?: () => void;
}

export const NotificationLike = ({ track, closeToast }: Props): ReactElement => {
    return (
        <BaseNotificationLike
            onClose={closeToast}
            entityVariant="track"
            entityTitle={track.title}
            collectionUrl="/collection"
            coverUri={track.coverUri}
            isLiked={track.isLiked}
        />
    );
};
