import { cast } from 'mobx-state-tree';

import { Artist } from '@yandex-music-int/resource-collection';

import { getSafeAverageColor } from '@/shared/lib/index.js';

import { ArtistModel } from '../model/Artist.js';

import { countsResponseToModel } from './countsResponseToModel.js';
import { decomposedArtistsResponseToModel } from './decomposedArtistsResponseToModel.js';

export const artistResponseToModel = (response: Artist): ArtistModel => {
    const decomposed = decomposedArtistsResponseToModel(response.decomposed);
    const counts = response?.counts ? countsResponseToModel(response.counts) : undefined;

    return cast({
        id: String(response.id),
        name: response.name,
        composer: response.composer,
        coverUri: response.cover?.uri,
        various: response.various,
        averageColor: getSafeAverageColor(response?.derivedColors),
        decomposed,
        counts
    });
};
