import { ReactElement, useCallback } from 'react';
import { useIntl } from 'react-intl';

import { Caption, CaptionSize } from '@yandex-music-int/sol/components/Typography';

import { useSearchFeedback } from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import { ArtistModel } from '../../model/Artist.js';
import { DecomposedArtistModel } from '../../model/DecomposedArtist.js';

interface Props {
    artist: ArtistModel | DecomposedArtistModel;
    withLink?: boolean;
    linkClassName?: string;
    captionClassName?: string;
    captionSize?: CaptionSize;
}

export const SeparatedArtistsBase = ({
    artist,
    withLink = true,
    linkClassName,
    captionClassName,
    captionSize = 'm'
}: Props): ReactElement => {
    const { formatMessage } = useIntl();
    const { sendNavigateSearchFeedback } = useSearchFeedback();
    withLink = withLink && !artist.various;

    const handleNavigate = useCallback(() => {
        sendNavigateSearchFeedback?.();
    }, [sendNavigateSearchFeedback]);

    if (withLink) {
        return (
            <Link
                aria-label={formatMessage({ id: 'entity-names.artist-name' }, { artistName: artist.name })}
                className={linkClassName}
                href={`/artist/${artist?.id}`}
                onClick={handleNavigate}
            >
                <Caption variant="span" type="entity" size={captionSize} weight="medium" className={captionClassName}>
                    {artist.name}
                </Caption>
            </Link>
        );
    }

    return (
        <Caption variant="span" type="entity" size={captionSize} weight="medium" className={captionClassName}>
            {artist.name}
        </Caption>
    );
};
