import { cast } from 'mobx-state-tree';

import { TrackParameters } from '@yandex-music-int/resource-collection';

import { TrackParametersModel } from '../model/TrackParameters.js';

export const trackParametersResponseToModel = (response?: TrackParameters): TrackParametersModel => {
    return cast({
        bpm: response?.bpm,
        energy: response?.energy && response?.energy / 100,
        hue: response?.hue && response?.hue / 360
    });
};
