import { ReactElement, ReactNode, useMemo } from 'react';

import { CaptionSize } from '@yandex-music-int/sol/components/Typography';

import { ArtistModel } from '../../model/Artist.js';
import { DecomposedArtistModel } from '../../model/DecomposedArtist.js';

import { SeparatedArtistsBase } from './SeparatedArtistsBase.js';

interface Props {
    artist: ArtistModel;
    withLink?: boolean;
    linkClassName?: string;
    captionClassName?: string;
    captionSize?: CaptionSize;
}

export const SeparatedArtistsItem = ({
    artist,
    withLink = true,
    linkClassName,
    captionClassName,
    captionSize
}: Props): ReactElement => {
    //  decomposedArtists   
    const decomposedArtists = useMemo(() => {
        return artist.decomposed?.reduce(
            (decomposedArtistsAccumulator: ReactNode[], decomposedArtist: DecomposedArtistModel) => {
                return decomposedArtistsAccumulator.concat([
                    decomposedArtist.separator,
                    <SeparatedArtistsBase
                        key={decomposedArtist.id}
                        artist={decomposedArtist}
                        withLink={withLink}
                        linkClassName={linkClassName}
                        captionClassName={captionClassName}
                        captionSize={captionSize}
                    />
                ]);
            },
            []
        );
    }, [artist.decomposed, captionClassName, linkClassName, withLink, captionSize]);

    return (
        <>
            <SeparatedArtistsBase
                artist={artist}
                withLink={withLink}
                linkClassName={linkClassName}
                captionClassName={captionClassName}
                captionSize={captionSize}
            />

            {decomposedArtists}
        </>
    );
};
