import { Instance, types } from 'mobx-state-tree';

import { BaseArtist } from './BaseArtist.js';
import { Counts } from './Counts.js';
import { DecomposedArtist } from './DecomposedArtist.js';

export const Artist = BaseArtist.props({
    coverUri: types.maybe(types.string),
    decomposed: types.maybe(types.array(DecomposedArtist)),
    averageColor: types.maybe(types.string),
    counts: types.maybe(Counts)
})
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => {
        return {
            get isAvailableForPlaying() {
                return !(self.counts?.tracks === 0);
            }
        };
    });

export type ArtistModel = Instance<typeof Artist>;
