import { types, Instance, getRoot } from 'mobx-state-tree';

import { ContentWarning, TrackSource } from '@yandex-music-int/resource-collection';
import { ExplicitMarkVariant } from '@yandex-music-int/sol/components/ExplicitMark';

import { TrackParameters } from './TrackParameters.js';

export const Track = types
    .model('Track', {
        id: types.string,
        // true,     
        isAvailable: types.boolean,
        // true,  UGC    
        isRemoved: types.boolean,
        title: types.string,
        contentWarning: types.maybe(types.enumeration(Object.values(ContentWarning))),
        trackSource: types.maybe(types.enumeration(Object.values(TrackSource))),
        version: types.maybe(types.string),
        durationMs: types.maybe(types.number),
        coverUri: types.maybe(types.string),
        averageColor: types.maybe(types.string),
        hslAverageColor: types.maybe(types.string),
        trackParametrs: types.maybe(TrackParameters)
    })
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => ({
        toggleLike() {
            // @ts-ignore
            const { library, user } = getRoot(self);

            if (user.isAuthorized) {
                return library.toggleTrackLike({
                    entityId: self.id,
                    userId: user.account.uid
                });
            }
        },
        toggleDislike() {
            // @ts-ignore
            const { library, user } = getRoot(self);

            if (user.isAuthorized) {
                return library.toggleTrackDislike({
                    entityId: self.id,
                    userId: user.account.uid
                });
            }
        }
    }))
    // eslint-disable-next-line @typescript-eslint/typedef
    .views((self) => ({
        get isLiked() {
            // @ts-ignore
            const { library } = getRoot(self);

            return library.isTrackLiked(self.id);
        },
        get isDisliked() {
            // @ts-ignore
            const { library } = getRoot(self);

            return library.isTrackDisliked(self.id);
        },
        get explicitMarkVariant() {
            /*
                TODO:
                      (     18+)
            */
            return self.contentWarning === ContentWarning.EXPLICIT ? ExplicitMarkVariant.E : null;
        },
        get isUGC() {
            return self.trackSource === TrackSource.UGC;
        }
    }));

export type TrackModel = Instance<typeof Track>;
