import { cast } from 'mobx-state-tree';

import { UgcTrack as UgcTrackResponse } from '@yandex-music-int/resource-collection';

import { UgcTrackModel } from '../model/UgcTrack.js';

import { trackResponseToModel } from './trackResponseToModel.js';

export const ugcTrackResponseToModel = (response: UgcTrackResponse | null | undefined): UgcTrackModel => {
    response = (response || {}) as UgcTrackResponse;
    return cast({
        ...trackResponseToModel(response),
        canPublish: response.canPublish,
        desiredVisibility: response.desiredVisibility,
        ugcArtistName: response.ugcArtistName
    });
};
