'use client';

import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';
import { FormattedMessage } from 'react-intl';

import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';
import { BaseNotification } from '@/shared/ui/index.js';

import styles from './NotificationDislike.module.scss';

interface Props extends WithClassName {
    coverUri?: string;
    title: string;
    isDisliked: boolean;
    closeToast?: () => void;
}

export const NotificationDislike = ({ coverUri, title, isDisliked, closeToast, className }: Props): ReactElement => {
    const cover = useMemo(() => {
        return (
            <Paper className={styles.cover} radius="s">
                <Image className={styles.image} src={coverUri} alt={title} size={100} fit="cover" withAvatarReplace />
            </Paper>
        );
    }, [coverUri, title]);

    const text = useMemo(() => {
        if (isDisliked) {
            return (
                <Caption className={styles.text} variant="div" type="controls" size="m">
                    <FormattedMessage id="notifications-info.track-unavailable-in-recommendations" />
                </Caption>
            );
        }

        return (
            <Caption className={styles.text} variant="div" type="controls" size="m">
                <FormattedMessage id="notifications-info.track-available-in-recommendations" />
            </Caption>
        );
    }, [isDisliked]);

    return (
        <BaseNotification
            className={clsx(styles.root, className)}
            onClose={closeToast}
            message={
                <div className={styles.message}>
                    {cover}
                    {text}
                </div>
            }
        />
    );
};
